        <el-form-item label="Áª¥Êä§Á±ªÂûã" prop="type">
          <el-select v-model="recordForm.type" placeholder="ËØ∑ÈÄâÊã©Áª¥Êä§Á±ªÂûã">
            <el-option label="Êó•Â∏∏Ê£ÄÊü•" value="regular_check" />
            <el-option label="ÁîµÊ±†Êõ¥Êç¢" value="battery_replacement" />
            <el-option label="ËΩÆËÉéÁª¥‰øÆ" value="tire_repair" />
            <el-option label="Âà∂Âä®Á≥ªÁªü" value="brake_system" />
            <el-option label="ÊéßÂà∂Âô®Áª¥‰øÆ" value="controller_repair" />
            <el-option label="ÂÖ®Èù¢Ê£Ä‰øÆ" value="full_maintenance" />
          </el-select>
        </el-form-item>
        
        <el-form-item label="ÊèèËø∞" prop="description">
          <el-input 
            v-model="recordForm.description" 
            type="textarea" 
            rows="3"
            placeholder="ËØ∑ËæìÂÖ•Áª¥Êä§ËØ¶ÊÉÖÊèèËø∞"
          />
        </el-form-item>
        
        <el-form-item label="ÂºÄÂßãÊó•Êúü" prop="startDate">
          <el-date-picker
            v-model="recordForm.startDate"
            type="datetime"
            placeholder="ÈÄâÊã©ÂºÄÂßãÊó•ÊúüÊó∂Èó¥"
            format="YYYY-MM-DD HH:mm"
            value-format="YYYY-MM-DD HH:mm"
          />
        </el-form-item>
        
        <el-form-item label="È¢ÑËÆ°ÂÆåÊàê" prop="endDate">
          <el-date-picker
            v-model="recordForm.endDate"
            type="datetime"
            placeholder="ÈÄâÊã©È¢ÑËÆ°ÂÆåÊàêÊó•ÊúüÊó∂Èó¥"
            format="YYYY-MM-DD HH:mm"
            value-format="YYYY-MM-DD HH:mm"
          />
        </el-form-item>
        
        <el-form-item label="Áä∂ÊÄÅ" prop="status">
          <el-select v-model="recordForm.status" placeholder="ËØ∑ÈÄâÊã©Áä∂ÊÄÅ">
            <el-option label="ËÆ°Âàí‰∏≠" value="planned" />
            <el-option label="ËøõË°å‰∏≠" value="in_progress" />
            <el-option label="Â∑≤ÂÆåÊàê" value="completed" />
            <el-option label="Â∑≤ÂèñÊ∂à" value="cancelled" />
          </el-select>
        </el-form-item>
        
        <el-form-item label="Áª¥Êä§‰∫∫Âëò" prop="technician">
          <el-select v-model="recordForm.technician" placeholder="ËØ∑ÈÄâÊã©Áª¥Êä§‰∫∫Âëò">
            <el-option label="Âº†Â∑•" value="Âº†Â∑•" />
            <el-option label="ÊùéÂ∑•" value="ÊùéÂ∑•" />
            <el-option label="ÁéãÂ∑•" value="ÁéãÂ∑•" />
            <el-option label="ËµµÂ∑•" value="ËµµÂ∑•" />
          </el-select>
        </el-form-item>
        
        <el-form-item label="Áª¥Êä§Ë¥πÁî®" prop="cost">
