<script setup>
import { ref, computed } from 'vue'
import { Plus, Search } from '@element-plus/icons-vue'
import { ElMessage, ElMessageBox } from 'element-plus'

// ÊêúÁ¥¢ÂíåÁ≠õÈÄâ
const searchQuery = ref('')
const statusFilter = ref('')
const dateRange = ref([])
const currentPage = ref(1)
const pageSize = ref(10)

// Ë°®ÂçïÁõ∏ÂÖ≥
const showAddDialog = ref(false)
const editingRecord = ref(null)
const formRef = ref(null)

const recordForm = ref({
  id: '',
  vehicleId: '',
  type: '',
  description: '',
  startDate: '',
  endDate: '',
  status: 'planned',
  technician: '',
  cost: 0
})

const rules = {
  vehicleId: [
    { required: true, message: 'ËØ∑ÈÄâÊã©ËΩ¶ËæÜ', trigger: 'change' }
  ],
  type: [
    { required: true, message: 'ËØ∑ÈÄâÊã©Áª¥Êä§Á±ªÂûã', trigger: 'change' }
  ],
  description: [
    { required: true, message: 'ËØ∑ËæìÂÖ•ÊèèËø∞', trigger: 'blur' }
  ],
  startDate: [
    { required: true, message: 'ËØ∑ÈÄâÊã©ÂºÄÂßãÊó•Êúü', trigger: 'change' }
  ],
  endDate: [
    { required: true, message: 'ËØ∑ÈÄâÊã©È¢ÑËÆ°ÂÆåÊàêÊó•Êúü', trigger: 'change' }
  ],
  status: [
    { required: true, message: 'ËØ∑ÈÄâÊã©Áä∂ÊÄÅ', trigger: 'change' }
  ],
  technician: [
    { required: true, message: 'ËØ∑ÈÄâÊã©Áª¥Êä§‰∫∫Âëò', trigger: 'change' }
  ]
}

// Ê®°ÊãüÊï∞ÊçÆ
const maintenanceRecords = ref([
  {
    id: 'M001',
    vehicleId: 'A0001',
    type: 'Êó•Â∏∏Ê£ÄÊü•',
    description: '‰æãË°åÊ£ÄÊü•ÂàπËΩ¶Á≥ªÁªüÂíåÁîµÊ±†Áä∂ÊÄÅ',
    startDate: '2024-03-15 09:00',
    endDate: '2024-03-15 11:00',
    status: 'completed',
    technician: 'Âº†Â∑•',
    cost: 50
  },
  {
    id: 'M002',
    vehicleId: 'B0002',
    type: 'ÁîµÊ±†Êõ¥Êç¢',
    description: 'Êõ¥Êç¢ËÄÅÂåñÁîµÊ±†',
    startDate: '2024-03-18 14:00',
    endDate: '2024-03-18 16:00',
    status: 'completed',
    technician: 'ÊùéÂ∑•',
    cost: 300
  },
  {
    id: 'M003',
    vehicleId: 'C0003',
    type: 'ËΩÆËÉéÁª¥‰øÆ',
    description: '‰øÆÂ§çÂâçËΩÆÊºèÊ∞îÈóÆÈ¢ò',
    startDate: '2024-03-20 10:00',
    endDate: '2024-03-20 12:00',
    status: 'in_progress',
    technician: 'ÁéãÂ∑•',
    cost: 80
  },
  {
    id: 'M004',
    vehicleId: 'A0005',
    type: 'ÂÖ®Èù¢Ê£Ä‰øÆ',
    description: 'ÂÖ®ËΩ¶Á≥ªÁªüÊ£Ä‰øÆÂíåÊ∏ÖÊ¥Å',
    startDate: '2024-03-25 09:00',
    endDate: '2024-03-25 17:00',
    status: 'planned',
    technician: 'ËµµÂ∑•',
    cost: 200
  }
])

// Ê®°ÊãüÂèØÈÄâËΩ¶ËæÜÊï∞ÊçÆ
const vehicles = ref([
  { id: 'A0001', name: 'ÁîµÂä®ÊªëÊùøËΩ¶ #A0001' },
  { id: 'B0002', name: 'ÁîµÂä®ÊªëÊùøËΩ¶ #B0002' },
  { id: 'C0003', name: 'ÁîµÂä®ÊªëÊùøËΩ¶ #C0003' },
  { id: 'A0004', name: 'ÁîµÂä®ÊªëÊùøËΩ¶ #A0004' },
  { id: 'A0005', name: 'ÁîµÂä®ÊªëÊùøËΩ¶ #A0005' },
  { id: 'B0006', name: 'ÁîµÂä®ÊªëÊùøËΩ¶ #B0006' }
])

// ËÆ°ÁÆóÂ±ûÊÄß
const totalRecords = computed(() => maintenanceRecords.value.length)

const filteredRecords = computed(() => {
  return maintenanceRecords.value.filter(record => {
    // ÊêúÁ¥¢ËøáÊª§
    const matchSearch = !searchQuery.value || 
      record.vehicleId.toLowerCase().includes(searchQuery.value.toLowerCase()) ||
      record.type.toLowerCase().includes(searchQuery.value.toLowerCase());
    
    // Áä∂ÊÄÅËøáÊª§
    const matchStatus = !statusFilter.value || record.status === statusFilter.value;
    
    // Êó•ÊúüËåÉÂõ¥ËøáÊª§
    let matchDate = true;
    if (dateRange.value && dateRange.value.length === 2) {
      const startDate = new Date(dateRange.value[0]);
      const endDate = new Date(dateRange.value[1]);
      const recordDate = new Date(record.startDate);
      
      matchDate = recordDate >= startDate && recordDate <= endDate;
    }
    
    return matchSearch && matchStatus && matchDate;
  });
})

// Áä∂ÊÄÅÁõ∏ÂÖ≥ÊñπÊ≥ï
function getStatusType(status) {
  const types = {
    'planned': 'info',
    'in_progress': 'warning',
    'completed': 'success',
    'cancelled': 'danger'
  }
  return types[status] || 'info'
}

function getStatusText(status) {
  const texts = {
    'planned': 'ËÆ°Âàí‰∏≠',
    'in_progress': 'ËøõË°å‰∏≠',
    'completed': 'Â∑≤ÂÆåÊàê',
    'cancelled': 'Â∑≤ÂèñÊ∂à'
  }
  return texts[status] || status
}

// Êìç‰ΩúÊñπÊ≥ï
function handleEdit(row) {
  editingRecord.value = row
  recordForm.value = { ...row }
  showAddDialog.value = true
}

function handleComplete(row) {
  ElMessageBox.confirm(
    `Á°ÆÂÆöÂ∞ÜÁª¥Êä§ËÆ∞ÂΩï ${row.id} Ê†áËÆ∞‰∏∫Â∑≤ÂÆåÊàêÂêóÔºü`,
    'Á°ÆËÆ§ÂÆåÊàê',
    {
      confirmButtonText: 'Á°ÆÂÆö',
      cancelButtonText: 'ÂèñÊ∂à',
      type: 'success'
    }
  ).then(() => {
    row.status = 'completed'
    row.endDate = new Date().toLocaleString('zh-CN', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit'
    }).replace(/\//g, '-')
    ElMessage.success('Áª¥Êä§ËÆ∞ÂΩïÂ∑≤Ê†áËÆ∞‰∏∫ÂÆåÊàê')
  })
}

function handleCancel(row) {
  ElMessageBox.confirm(
    `Á°ÆÂÆöË¶ÅÂèñÊ∂àÁª¥Êä§ËÆ∞ÂΩï ${row.id} ÂêóÔºü`,
    'Á°ÆËÆ§ÂèñÊ∂à',
    {
      confirmButtonText: 'Á°ÆÂÆö',
      cancelButtonText: 'ÂèñÊ∂à',
      type: 'warning'
    }
  ).then(() => {
    row.status = 'cancelled'
    ElMessage.success('Áª¥Êä§ËÆ∞ÂΩïÂ∑≤ÂèñÊ∂à')
  })
}

async function handleSubmit() {
  if (!formRef.value) return
  
  await formRef.value.validate((valid) => {
    if (valid) {
      if (editingRecord.value) {
        // Êõ¥Êñ∞Áé∞ÊúâËÆ∞ÂΩï
        const index = maintenanceRecords.value.findIndex(r => r.id === editingRecord.value.id)
        if (index > -1) {
          maintenanceRecords.value[index] = { ...recordForm.value }
        }
      } else {
        // Ê∑ªÂä†Êñ∞ËÆ∞ÂΩï
        const newId = 'M' + String(maintenanceRecords.value.length + 1).padStart(3, '0')
        maintenanceRecords.value.push({
          ...recordForm.value,
          id: newId
        })
      }
      
      showAddDialog.value = false
      editingRecord.value = null
      resetForm()
      ElMessage.success(editingRecord.value ? 'Êõ¥Êñ∞ÊàêÂäü' : 'Ê∑ªÂä†ÊàêÂäü')
    }
  })
}

function resetForm() {
  recordForm.value = {
    id: '',
    vehicleId: '',
    type: '',
    description: '',
    startDate: '',
    endDate: '',
    status: 'planned',
    technician: '',
    cost: 0
  }
}
</script>

<style scoped>
